
import { Options, Vue, setup } from "vue-class-component";
import { RouterView, useRouter, Router } from "vue-router";
import { Dialog, Toast } from "vant";
import { onMounted, ref } from "vue";
import { useStore } from "vuex";
import conn from "./initIm";
import { ERROR_CODE } from "@/const/errorCode";
import { getMessageFromId } from "@/utils/im";
import { CHAT_TYPE } from "./const";
import { AllState } from "@/store";
import { formatTime } from "@/utils";
import websdk from "easemob-websdk";

let intervalIndex = 1;

const DNS_LIST = [
  "https://rs.easemob.com",
  "https://rs.chat.agora.io",
  "https://rs1.easemob.com",
  "https://rs2.easemob.com",
  "https://rs3.easemob.com",
  "https://rs4.easemob.com"
  // "http://59.110.89.59",
  // "http://39.97.193.190",
  // "http://39.97.193.187"
];

const REST_URL = ["https://a1.easemob.com/easemob/easeim/token"];

function getRandomDelay(min: number, max: number) {
  return Math.floor(Math.random() * (max - min + 1)) + min;
}

const getUserToken = (apiUrl: string) => {
  console.log(formatTimestamp(), "try request token", apiUrl);
  const params = {
    grant_type: "password",
    username: "sttest",
    password: "123",
    timestamp: +new Date()
  };
  websdk.utils
    .ajax({
      headers: {
        "Content-type": "application/json"
      },
      url: apiUrl,
      dataType: "json",
      data: JSON.stringify(params)
    })
    .then((res: any) => {
      console.log(formatTimestamp(), "request token success", res, apiUrl);
    })
    .catch((e: any) => {
      console.error(formatTimestamp(), "request token failed", e, apiUrl);
    });
};

const getHttpDNS = (apiUrl: string) => {
  console.log(formatTimestamp(), "request dns", apiUrl);
  websdk.utils
    .ajax({
      url: apiUrl,
      dataType: "json",
      type: "GET"
    })
    .then((res: any) => {
      console.log(formatTimestamp(), "request dns success", res, apiUrl);
    })
    .catch((e: any) => {
      console.log(formatTimestamp(), "request dns failed", e, apiUrl);
      REST_URL.forEach((url) => {
        getUserToken(url);
      });
    });
};

const getDNS = () => {
  DNS_LIST.forEach((url) => {
    // 生成随机延迟时间（例如在 1 到 3 秒之间）
    const delay = getRandomDelay(1000, 3000);
    setTimeout(() => {
      getHttpDNS(
        `${url}/easemob/server.json?app_key=${encodeURIComponent(
          "easemob#easeim"
        )}&_v=${new Date().getTime()}`
      );
    }, delay);
  });
};

function formatTimestamp() {
  const now = new Date();

  const year = now.getFullYear();
  const month = String(now.getMonth() + 1).padStart(2, "0");
  const day = String(now.getDate()).padStart(2, "0");

  const hours = String(now.getHours()).padStart(2, "0");
  const minutes = String(now.getMinutes()).padStart(2, "0");
  const seconds = String(now.getSeconds()).padStart(2, "0");

  return `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
}

@Options({
  components: {
    RouterView
